
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Upload from '@/components/Upload'
import avatarUrl from '../assets/logo.png'
let heights = []
let weights = []
for (var i = 150; i <= 200; i++) {
heights.push({
name: i,
value: i
})
}
for (var k = 40; k <= 100; k++) {
weights.push({
name: k,
value: k
})
}
export default {
components: {
Group,
XInput,
PopupPicker,
XAddress,
Datetime,
Upload,
XButton
},
data () {
return {
o: {
a: 1,
b: 2
},
form: {
nickname: '',
sex: ['1'],
birth: '',
height: [],
weight: [],
adsValue: ['330000', '330200'],
desc: '',
avatar_url: avatarUrl
},
sexs: [[{
name: '男',
value: '1'
}, {
name: '女',
value: '2'
}]],
heights: [heights],
weights: [weights],
addressData: ChinaAddressData
}
},
methods: {
afterClip (base64) {
this.form.avatar_url = base64
}
}
}



